table{
  margin: 20px 0;
  border: 1px solid black;
  border-collapse: collapse;
}
th, td{
  padding: 0 15px;
  border: 1px solid black;
  font-size: 16px;
}
th a, td a{
  text-decoration: none;
  color: black;
}
th a:hover, td a:hover{
  text-decoration: underline;
  color: red;
}
.container{
  display: -webkit-flex;
  display: flex;
  width: 350px;
}
.container p{
  margin: 0;
}
.block{
  width: 90px;
  height: 20px;
  margin-right: 5px;
  text-align: center;
}
.Checked{
  background: green;
}
.Checking{
  background: pink;
}
.InProgress{
  background: yellow;
}
.ToDo{
  background: grey;
}
.Check{
  background: green;
}
.fail{
  background: darkred;
}
